.movie {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 300px;
  border-radius: 7px;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.2);
  margin: 8px;
}

.movie__img {
  width: 50%;
  margin-top: 8px;
  object-fit: cover;
  border-radius: 20px;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.2);
}

.movie__text > * {
  text-align: center;
  margin: 23px 14px;
}

.movie__text h1 {
  font-weight: 600;
}
