.detail {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  width: 70%;
}

.detail img {
  width: auto;
  height: auto;
  object-fit: cover;
  border-radius: 20px;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.2);
}
.detail__text {
  margin: 7px;
}

.detail__text h2 {
  font-size: 30px;
  font-weight: 700;
}

.detail__text_content > * {
  padding: 5px;
  margin-top: 10px;
}
